'use strict';

//#region –°–æ–∑–¥–∞–Ω–∏–µ –≤–µ—Ä—Å—Ç–∫–∏
// –ü–æ–ª—É—á–∞–µ–º –≥–æ–ª–æ–≤–Ω–æ–π —ç–ª–µ–º–µ–Ω—Ç
const app = document.querySelector('.app');

const headerText = `–ü—Ä–æ—Å—Ç–µ–π—à–∏–π –ö–∞–ª—å–∫—É–ª—è—Ç–æ—Ä<br />
                    <h5>–¥–ª—è –¥–≤—É—Ö —á–∏—Å–µ–ª</h5>`;
const footerText = `–°–æ–∑–¥–∞–Ω–æ –≤ —É—á–µ–±–Ω—ã—Ö —Ü–µ–ª—è—Ö —Å –ø–æ–º–æ—â—å—é<br />
                    HTML, SCSS(SASS) –∏ JavaScript
                    <br />
                    <br />
                    Asmadeus, 2021 –≥.`;
const iconArr = [
    '<i class="fas fa-divide"></i>', // 0 - –î–µ–ª–µ–Ω–∏–µ
    '<i class="fas fa-times"></i>', // 1 - –£–º–Ω–æ–∂–µ–Ω–∏–µ
    '<i class="fas fa-minus"></i>', // 2 - –ú–∏–Ω—É—Å
    '<i class="fas fa-plus"></i>', // 3 - –ü–ª—é—Å
    '<i class="fas fa-equals"></i>', // 4 - –†–∞–≤–Ω–æ
];
const operSelectors = [
    'divide', // 0 - –ü–ª—é—Å
    'multiply', // 1 - –ú–∏–Ω—É—Å
    'minus', // 2 - –£–º–Ω–æ–∂–µ–Ω–∏–µ
    'plus', // 3 - –î–µ–ª–µ–Ω–∏–µ
    'result' // 4 - –†–∞–≤–Ω–æ
];
const btnArr = [
    '7',
    '8',
    '9',
    '4',
    '5',
    '6',
    '1',
    '2',
    '3',
    '.',
    '0',
    '<i class="fas fa-trash"></i>'
];

function createElement(element, selector, parent, inHTML) {
    const elem = document.createElement(element);
    elem.classList.add(selector);
    elem.innerHTML = inHTML;
    parent.append(elem);
}

function crtNumBtn(parent, sel, inText) {
    const div = document.createElement('button');
    div.classList.add('num__btn', sel);
    div.innerHTML = inText;
    parent.append(div);
}

function crtOperBtn(parent, inText, operSelectors) {
    const div = document.createElement('button');
    div.classList.add('oper__btn', operSelectors);
    div.innerHTML = inText;
    parent.append(div);
}
// –°–æ–∑–¥–∞–µ–º –ó–∞–≥–æ–ª–æ–≤–æ–∫
createElement('header', 'header', app, headerText);
// –°–æ–∑–¥–∞–µ–º "–ö–æ—Ä–ø—É—Å –ö–∞–ª—å–∫—É–ª—è—Ç–æ—Ä–∞"
createElement('div', 'calc', app, '');
// –°–æ–∑–¥–∞–µ–º –õ–µ–≤—É—é —á–∞—Å—Ç—å
// –°–æ–∑–¥–∞–µ–º –ë–ª–æ–∫ –î–∏—Å–ø–ª–µ—è –Ω–∞ –ö–æ—Ä–ø—É—Å–µ
createElement('div', 'calc__display', app.children[1], '');
// –°–æ–∑–¥–∞–µ–º –î–∏—Å–ø–ª–µ–π –≤ –±–ª–æ–∫–µ –î–∏—Å–ø–ª–µ—è
createElement('div', 'display__display', app.children[1].children[0], '');
// –°–æ–∑–¥–∞–µ–º –ö–Ω–æ–ø–∫—É –∏—Å—Ç–æ—Ä–∏–∏ –≤ –±–ª–æ–∫–µ –î–∏—Å–ø–ª–µ—è
createElement('button', 'display__historyBtn', app.children[1].children[0], '<i class="fas fa-chevron-right"></i>');
// –°–æ–∑–¥–∞–µ–º –ö–Ω–æ–ø–æ—á–Ω—ã–π –±–ª–æ–∫ –Ω–∞ –ö–æ—Ä–ø—É—Å–µ
createElement('div', 'calc__buttons', app.children[1], '');
// –°–æ–∑–¥–∞–µ–º —Ü–∏–∫–ª–æ–º –ö–Ω–æ–ø–∫–∏ –≤ —Ü–∏—Ñ—Ä–æ–≤–æ–º –±–ª–æ–∫–µ
createElement('div', 'btns__num', app.children[1].children[1], '');
for (let i = 0; i < 12; i++) {
    const div = app.children[1].children[1].children[0];
    crtNumBtn(div, 'num__btn', btnArr[i]);
}
// –°–æ–∑–¥–∞–µ–º —Ü–∏–∫–ª–æ–º –ö–Ω–æ–ø–∫–∏ –£–ø—Ä–∞–≤–ª–µ–Ω–∏—è
createElement('div', 'btns__oper', app.children[1].children[1], '');
for (let i = 0; i < 5; i++) {
    const div = app.children[1].children[1].children[1];
    crtOperBtn(div, iconArr[i], operSelectors[i]);
}
// –°–æ–∑–¥–∞–µ–º –ü–æ–¥–≤–∞–ª
createElement('footer', 'footer', app, footerText);
//#endregion

//#region –û–±—Ä–∞–±–æ—Ç—á–∏–∫–∏ –∏ —Ñ—É–Ω–∫—Ü–∏–æ–Ω–∞–ª

//#region –ü–æ–ª—É—á–µ–Ω–∏–µ —ç–ª–µ–º–µ–Ω—Ç–æ–≤ —Å–æ —Å—Ç—Ä–∞–Ω–∏—Ü—ã
// –ö–Ω–æ–ø–∫–∏ –ú–∞—Ç. –û–ø–µ—Ä–∞—Ü–∏–π
const calcOperMinus = document.querySelector('.minus');
const calcOperMultiply = document.querySelector('.multiply');
const calcOperDivide = document.querySelector('.divide');
const calcOperResult = document.querySelector('.result');
// –°–ø–∏—Å–æ–∫ –≤—Å–µ—Ö –∫–Ω–æ–ø–æ–∫ –≤ –≤–∏–¥–µ –ø—Å–µ–≤–¥–æ–º–∞—Å—Å–≤–∏–∞
const calcNums = document.querySelectorAll('.num__btn');
const calcOpers = document.querySelectorAll('.oper__btn');
// –î–∏—Å–ø–ª–µ–π
const display = document.querySelector('.display__display');
// –î–æ–±–∞–≤–ª–µ–Ω–∏—è —Å–µ–ª–µ–∫—Ç–æ—Ä–∞ CLN –∫–Ω–æ–ø–∫–µ –æ—á–∏—â–µ–Ω–∏—è
//#endregion

calcNums[11].classList.add('CLN');

function cleanDisplay() {
    display.innerHTML = '';
}

function numToDisplay(item) {
    display.innerHTML += item.innerHTML;
}

function numAfterDOT(number) {
    return (parseFloat(number).toFixed(2));
}

function calculate() {
    const calcArr = display.innerHTML.split(/(-?\d+(?:\.\d+)?)\s*([-+*\/])\s*(-?\d+(?:\.\d+)?)/gm);
    let mathResult;
    calcArr[1] = parseFloat(calcArr[1]);
    calcArr[3] = parseFloat(calcArr[3]);
    if (calcArr[2] == '/') {
        if (calcArr[3] == 0) {
            mathResult = `–î–µ–ª–∏—Ç—å –Ω–∞ 0 –Ω–µ–ª—å–∑—è!`;
        } else {
            mathResult = calcArr[1] / calcArr[3];
        }
    }
    if (calcArr[2] == '*') {
        mathResult = calcArr[1] * calcArr[3];
    }
    if (calcArr[2] == '-') {
        mathResult = calcArr[1] - calcArr[3];
    }
    if (calcArr[2] == '+') {
        mathResult = calcArr[1] + calcArr[3];
    }
    if (typeof display.innerHTML == 'string') {
        display.innerHTML = mathResult;
    } else {
        display.innerHTML = numAfterDOT(mathResult);
    }
    console.log(calcArr);
    console.log(`–†–µ–∑—É–ª—å—Ç–∞—Ç : ${numAfterDOT(mathResult)}`);
}

function addELtoNums() {
    calcNums.forEach(item => {
        if (!item.classList.contains('CLN')) {
            item.addEventListener('click', () => {
                numToDisplay(item);
            });
        }
    });
    calcNums[11].addEventListener('click', cleanDisplay);
}

function addELtoOpers() {
    calcOpers.forEach(item => {
        if (!item.classList.contains('Result')) {
            item.addEventListener('click', () => {
                if (item.classList.contains('plus')) {
                    display.innerHTML += '+';
                }
                if (item.classList.contains('minus')) {
                    display.innerHTML += '-';
                }
                if (item.classList.contains('multiply')) {
                    display.innerHTML += '*';
                }
                if (item.classList.contains('divide')) {
                    display.innerHTML += '/';
                }
            });
        }
    });
    calcOpers[4].addEventListener('click', calculate);
}

function removeELtoNums() {
    calcNums.forEach(item => {
        if (!item.classList.contains('CLN')) {
            item.removeEventListener('click', () => {
                numToDisplay(item);
            });
        }
    });
}

function removeELtoOpers() {
    calcOpers.forEach(item => {
        if (!item.classList.contains('Result')) {
            item.removeEventListener('click', () => {
                if (item.classList.contains('plus')) {
                    display.innerHTML += '+';
                }
                if (item.classList.contains('minus')) {
                    display.innerHTML += '-';
                }
                if (item.classList.contains('multiply')) {
                    display.innerHTML += '*';
                }
                if (item.classList.contains('divide')) {
                    display.innerHTML += '/';
                }
            });
        }
    });
    calcOpers[4].removeEventListener('click', calculate);
}

addELtoNums();
addELtoOpers();
//#endregion